div.event-details-container {
  background-color: #092130;
  text-align: center;
  padding-top: 25%;
  padding-bottom: 50px;
}

div.event-title {
  margin-top: 32px;
  margin-bottom: 25px;
  font-size: 2em;
  color: #67571F;
}

div.event-listing {
  font-size: 2.2em;
  color: #FCFAEF;
}

span.performance-type {
  font-size: 0.8em;
  color: #445C81;
}

div.event-details-specifics {
  margin-top: 25px;
  font-size: 2em;
  color: #67571F;
}

div.event-details-specifics-date {
  font-size: 1.2em;
}

div.event-facebook {
  margin-top: 18px;
}

@media only screen and (max-width : 1200px) {

  div.event-details-container {
    padding-top: 8em;
    width: 100%;
  }

  div.event-details-container {
    padding-top: 10%;
  }

  div.event-title {
    font-size: 6em;
  }

  div.event-listing {
    font-size: 4.2em;
  }

  div.event-details-specifics {
    margin-top: 0.8em;
    font-size: 4em;
  }

  div.event-details-specifics-date {
    font-size: 1.2em;
  }

  div.container {
    margin-left: 0;
    margin-right: 0;
    padding-left: 0;
    padding-right: 0;
    width: 100%;
  }

  div.event-facebook img {
    height: 4em;
  }
}

@media only screen and (max-width : 768px) {

  div.event-details-container {
    padding-top: 4em;
    width: 100%;
  }

  div.event-title {
    font-size: 3em;
  }

  div.event-listing {
    font-size: 2.2em;
  }

  div.event-details-specifics {
    margin-top: 0.8em;
    font-size: 3em;
  }

  div.event-details-specifics-date {
    font-size: 1.2em;
  }

  div.container {
    margin-left: 0;
    margin-right: 0;
    padding-left: 0;
    padding-right: 0;
    width: 100%;
  }

  div.event-facebook img {
    height: 3em;
  }
}




