div.home-blurb-container {
  background-color: #04221D;
  text-align: center;
  padding-top: 31%;
  padding-bottom: 50px;
  padding-left: 100px;
  padding-right: 100px;
}

div.home-guarda-records {
  margin-top: 15px;
  margin-bottom: 15px;
}

div.home-guarda-records img.records {
  height: 1.6em;
}

div.home-guarda-blurb {
  margin-top: 46px;
  color: #FCFAEF;
  font-size: 2em;
}

div.home-guarda-social {
  display: inline-block;
  margin: 0 auto;
  margin-top: 17px;
}

div.home-guarda-social img {
  float: left;
  margin-right: 15px;
}

img.social_right_img {
  margin-right: 0px;
}

div.home-guarda-social .clear {
  clear: left;
}

@media only screen and (max-width : 1200px) {

  div.container {
    margin-top: 0;
    margin-left: 0;
    margin-right: 0;
    padding-left: 0;
    padding-right: 0;
    width: 100%;
  }

  img.home-guarda-logo {
    height: 32em;
  }

  div.home-guarda-records img.records {
    height: 4em;
  }

  div.home-guarda-blurb {
    font-size: 4em;
    margin-bottom: 1.2em;
  }

  div.home-guarda-records {
    margin-top: 6em;
    margin-bottom: 6em;
  }

  div.home-blurb-container {
    padding-top: 10em;
    width: 100%;
  }

  img.social_img, img.social_right_img {
    height: 4em;
  }
}

@media only screen and (max-width : 768px) {
  img.home-guarda-logo {
    height: 16em;
  }

  div.home-guarda-records img.records {
    height: 2em;
  }

  div.home-guarda-blurb {
    font-size: 2em;
  }

  div.home-guarda-records {
    margin-top: 4em;
    margin-bottom: 4em;
  }

  div.home-blurb-container {
    padding-top: 6em;
    width: 100%;
  }

  img.social_img, img.social_right_img {
    height: 2.4em;
  }
}



