html {
  margin: 0;
  padding: 0;
}

body {
  background-color: #092130;
  font-family: 'Dosis', sans-serif;
  font-size: 0.9em;
  margin: 0;
  padding: 0;
}

#app {

}

img {
  border: 0;
}

div.background-left {
  background-color: #04221D;
  position: absolute;
  z-index: -1;
  width: 50%;
  height: 100%;
  display: inline;
}

div.background-right {
  background-color: #092130;
  position: absolute;
  z-index: -1;
  width: 50%;
  left: 50%;
  height: 100%;
  display: inline;
}

div.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
}

img.footer-img {
    max-width: 50%;
    height: auto;
    width: auto\9;
}

div.mobile-section1-footer {
  background-color: #04221D;
  position:absolute;
  bottom: 0;
  display: none;
}

div.mobile-section2-footer {
  background-color: #092130;
  display: none;
}

img.mobile-footer-img {
    max-width: 100%;
    height: auto;
    width: 100%;
}

.show {
  display: block;
}

.hide {
  display: none;
}

@media only screen and (max-width : 1200px) {

  div.background-right {
    display: none;
  }

  div.background-left {
    display: none;
  }

  div.mobile-section1-footer {
    display: block;
  }

  div.mobile-section2-footer {
    display: block;
  }

  .footer {
   display: none;
  }
}



